

















































import { Component, Vue } from 'vue-property-decorator'
import api, { LoginReq, setUser } from '../api'
import env from 'env'

@Component
export default class Login extends Vue {
    public $refs!: {
        form: Vue & {
            // @ts-ignore
            validate(Function): void
        }
    }

    protected isLoading = false

    protected data: LoginReq = {
        name: '',
        password: ''
    }

    get title() {
        return env.title
    }

    protected rules = {
        name: [{ required: true }],
        password: [{ required: true }]
    }

    protected login() {
        this.$refs.form.validate(async (valid: boolean) => {
            if (!valid) {
                return
            }
            this.isLoading = true
            try {
                let user = await api.login(this.data)
                setUser(user)
                this.$router.push({
                    name: 'memberLog'
                })
            } catch (error) {
                this.isLoading = false
            }
        })
    }
}
