






















































































import { Component, Vue } from 'vue-property-decorator'
import env from 'env'
import { getUser, setUser } from './api'

@Component
export default class App extends Vue {
    get isLogin() {
        return this.$route.name === 'login'
    }

    get user() {
        return getUser()
    }

    get title() {
        return env.title
    }

    protected logout() {
        setUser()
        this.$router.push({
            name: 'login'
        })
    }

}
